<template>
	<div class="sub-container">
		<ul class="tabs sub">
			<li @click="goto('/admin/youtube')" class="current">유튜브</li>
			<li @click="goto('/admin/column')">칼럼</li>
			<li @click="goto('/admin/writer')">칼럼 작성자</li>
		</ul>

		<div class="contents-container">
			<p class="contents-title underline marB-20 body3">유튜브 콘텐츠 등록</p>
			<div class="input-container marB-40">
				<section class="marB-40">
					<div class="input-item align-top">
						<AddImageSingle
							v-if="thumb"
							:refName="'mainImage'"
							@mainImage="setMainImage"
							:image="thumb"
						/>
					</div>
				</section>

				<section class="basic-info">
					<div class="input-item">
						<p class="small-title">유튜브 영상 업로드 일자</p>
						<input
							type="text"
							class="admin-input small"
							name="contents"
							placeholder="YYYY-MM-DD"
							v-model="uploadDate"
						/>
					</div>

					<div class="input-item">
						<p class="small-title">제목</p>
						<input
							type="text"
							class="admin-input medium"
							name="title"
							v-model="title"
						/>
					</div>
					<div class="input-item">
						<p class="small-title">설명 문구</p>
						<input
							type="text"
							class="admin-input medium"
							name="contents"
							v-model="contents"
						/>
					</div>
					<div class="input-item">
						<p class="small-title">영상 링크</p>
						<input
							type="text"
							class="admin-input medium"
							name="link"
							v-model="link"
						/>
					</div>
				</section>

				<button class="outlined-btn body3" @click="updateYoutube">
					등록
				</button>
			</div>
		</div>
	</div>
</template>

<script>
import AddImageSingle from '@/components/common/AddImageSingle';
import { mapGetters } from 'vuex';
import Store from '@/store';
import { updateYoutube, fetchYoutubeDetail } from '@/api/youtube';

export default {
	components: { AddImageSingle },
	computed: {
		...mapGetters(['main']),
	},
	created() {
		Store.commit('SET_MAIN', null);
		this.id = this.$route.params.id;
		this.fetchYoutubeDetail(this.id);
	},
	data: () => ({
		title: '',
		link: '',
		uploadDate: '',
		contents: '',
		errMsg: '',
		id: '',
		thumb: '',
	}),
	methods: {
		goto(url) {
			this.$router.push({ path: url });
		},
		async fetchYoutubeDetail(id) {
			try {
				let { data } = await fetchYoutubeDetail(id);
				this.title = data.title;
				this.contents = data.contents;
				this.link = data.link;
				this.uploadDate = data.uploadDate;
				this.thumb = {
					imgPath: data.imgPath,
					imgName: data.imgName,
				};
			} catch (e) {
				console.log(e);
			}
		},
		async updateYoutube() {
			if (!this.thumb) {
				this.errMsg = '이미지를 등록해주세요';
				alert(this.errMsg);
				return false;
			}

			if (!this.title) {
				this.errMsg = '제목을 입력해주세요';
				alert(this.errMsg);
				return false;
			}
			if (!this.link) {
				this.errMsg = '링크를 입력해주세요';
				alert(this.errMsg);
				return false;
			}
			if (!this.uploadDate) {
				this.errMsg = '업로드한 날짜를 입력해주세요';
				alert(this.errMsg);
				return false;
			}
			if (!this.contents) {
				this.errMsg = '설명 문구를 입력해주세요';
				alert(this.errMsg);
				return false;
			}

			let data = new FormData();
			if (this.main) {
				console.log('?');
				data.append('file', this.main);
			}
			data.append('title', this.title);
			data.append('contents', this.contents);
			data.append('link', this.link);
			data.append('uploadDate', this.uploadDate);
			let result;
			try {
				result = await updateYoutube(this.id, data);
				if (result.data.result == true) {
					alert('유튜브가 수정되었습니다');
				} else if (result.data.result == false) {
					alert('에러가 발생했습니다');
				}
			} catch (e) {
				alert('에러가 발생했습니다');
			}
		},
		setMainImage(data) {
			Store.commit('SET_MAIN', data);
			this.thumb = this.main;
		},
		isEmptyObject(param) {
			return Object.keys(param).length === 0 && param.constructor === Object;
		},
	},
};
</script>

<style scoped>
.basic-info {
	margin-bottom: 40px;
}

.options {
	margin-bottom: 40px;
}
</style>
